import {useState, useEffect} from "react";
import css from './standingorder.module.scss';


const StandingOrder = () =>{
const {data, setData} = useState([]);
useEffect(()=>{
    fetchData();
})


const fetchData = ()=>{
  const data =  fetch("http://localhost:4000/standingorders", {
      method:get,
      headers: {
        'Content-Type': 'application/json'
      },
  });
  console.log("standing order: \n" +  data);
}

return(
    <div>
        <ul>
          {data.map((item)=>{
              return <li>{item.productId}</li>
          })}
        </ul>
    </div>
)
}

export default StandingOrder;